import React, {Component} from 'react';
import {TiSocialLinkedin, TiSocialGithub, TiWorld, TiLocation, TiMail, TiPhone} from 'react-icons/ti';

export default class Header extends Component {
  render () {
    return (
      <div className="header">
        <Intro />
        <ContactInfo />
      </div>
    );
  }
}

const Intro = () => (
  <div className="intro">
    <div className="me">
      <div className="name"><div>Youssef CHIHI</div></div>
      <div className="profession"><div>Front-End Engineer</div></div>
    </div>
    <div className="social-links">
      <a href="https://www.linkedin.com/in/youssef-chihi" className="social" target="_blank">
        <div className="icon">
          <TiSocialLinkedin />
        </div>
        <div className="label">/in/youssef-chihi</div>
      </a>
      <a href="https://github.com/YoucefChihi" className="social" target="_blank">
        <div className="icon">
          <TiSocialGithub />
        </div>
        <div className="label">/YoucefChihi</div>
      </a>
      <a href="https://youcefchihi.github.io" className="social">
        <div className="icon">
          <TiWorld />
        </div>
        <div className="label">youcefchihi.github.io</div>
      </a>
    </div>
  </div>
);

const ContactInfo = () => (
  <div className="contact-info">
    <div className="contact">
      <div className="icon">
        <TiLocation />
      </div>
      <div className="label">Tunis, Tunisia</div>
    </div>
    <div className="contact">
      <div className="icon">
        <TiPhone />
      </div>
      <div className="label">(+216) 25 757 103</div>
    </div>
    <a
      href="mailto:youcef.chihi@gmail.com?Subject=Hello"
      target="_top"
      className="contact"
    >
      <div className="icon">
        <TiMail />
      </div>
      <div className="label">youcef.chihi@gmail.com</div>
    </a>
  </div>
);



