import React, {Component} from 'react';
import {TiCode, TiMortarBoard, TiLightbulb, TiArrowUp} from 'react-icons/ti';
import {ReduxIcon, GraphQLIcon} from '../Icons';

const skills = [
  {
    label: 'React.js',
    icon: <i className="devicon-react-original" />,
  },
  {
    label: 'Redux',
    icon: ReduxIcon,
  },
  {
    label: 'Git',
    icon: <i className="devicon-git-plain" />,
  },
  {
    label: 'GraphQL',
    icon: GraphQLIcon,
  },
  {
    label: 'Nodejs',
    icon: <i className="devicon-nodejs-plain" />,
  },
  {
    label: 'PHP',
    icon: <i className="devicon-php-plain" />,
  },
  {
    label: 'MySQL',
    icon: <i className="devicon-mysql-plain" />,
  },
  {
    label: 'MongoDB',
    icon: <i className="devicon-mongodb-plain" />,
  },
];
const projects = [
  {
    title: 'WiseSpend',
    description: 'Hybrid mobile app built on Cordova for managing daily expenses',
    url: 'https://github.com/YoucefChihi/wise-spend'
  },
  {
    title: 'Alwan El Aql',
    description: 'A puzzle game destined for arabic users, inspired by Lumosity.',
    url: 'https://github.com/YoucefChihi/alwan_el_aql'
  },
]
export default class LeftSection extends Component {
  render () {
    return (
      <div className="left-section">
        <Projects data={projects}/>
        <Skills data={skills}/>
        <Education />
      </div>
    );
  }
}

const Projects = ({data}) => (
  <div className="sub-section projects">
    <div className="head">
      <div className="icon"><TiLightbulb /></div>
      <div className="label">Projects</div>
    </div>
    <div className="body">
      {data.map((itm, idx) => (
        <a href={itm.url} key={`project-${idx}`} className="project" target="_blank">
          <div className="title">
            {itm.title}
            <div className="icon">
              <div className="border"></div>
              <div className="arrow">
                <TiArrowUp />
              </div>
            </div>
          </div>
          <div className="description">
            {itm.description}
          </div>
        </a>
      ))}
    </div>
  </div>
);

const Skills = ({data}) => (
  <div className="sub-section skills">
    <div className="head">
      <div className="icon"><TiCode /></div>
      <div className="label">Skills</div>
    </div>
    <div className="body">
      {data.map ((itm, idx) => (
        <div className="item" key={`skill-${idx}`}>
          <div className="icon">{itm.icon}</div>
          <div className="label">{itm.label}</div>
        </div>
      ))}
    </div>
  </div>
);

const Education = () => (
  <div className="sub-section education">
    <div className="head">
      <div className="icon"><TiMortarBoard /></div>
      <div className="label">Education</div>
    </div>
    <div className="body">
      <div className="degree">
        <div className="col1">
          <div className="title">M.S. Information Systems Security</div>
          <div className="school">Higher Institute of Management</div>
        </div>
        <div className="col2">
          <div className="year">2018</div>
          <div className="location">Tunis</div>
        </div>
      </div>
      <div className="degree">
        <div className="col1">
          <div className="title">B.S. Computer Science</div>
          <div className="school">Higher Institute of Management</div>
        </div>
        <div className="col2">
          <div className="year">2016</div>
          <div className="location">Tunis</div>
        </div>
      </div>
    </div>
  </div>
);



