import React, {Component} from 'react';
import {TiBeaker} from 'react-icons/ti';
import cs from 'classnames';
import {
  ReduxIcon,
  D3jsIcon,
  HbaseIcon,
  GraphQLIcon,
} from '../Icons'

const experience = [
  {
    title: 'Front-End Engineer',
    company: 'Schibsted Classified Media',
    company_url: 'https://www.schibsted.com',
    date_from: 'JULY 2018',
    date_to: 'PRESENT',
    tasks: [
      'Collaborated with remote team in europe on new features using React.js, Redux and Apollo GraphQL.',
      'Wrote Nodejs scripts to parse and validate resumes automatically.'
    ],
    tech_used: [
      {
        icon: <i className="devicon-react-original" />,
        label: 'React',
      },
      {
        icon: ReduxIcon,
        label: 'Redux',
      },
      {
        icon: GraphQLIcon,
        label: 'GraphQL',
      },
      {
        icon: <i className="devicon-nodejs-plain" />,
        label: 'Nodejs',
      },
      {
        icon: <i className="devicon-php-plain"></i>,
        label: 'PHP'
      },
      {
        icon: <i className="devicon-mysql-plain"></i>,
        label: 'MySQL',
      },
    ],
  },
  {
    title: 'Full-Stack Developer',
    company: 'Barac',
    company_url: 'https://barac.io',
    date_from: 'JULY 2017',
    date_to: 'JUIN 2018',
    tasks: [
      'Developed user friendly UI with React.js and Redux.',
      'Designed a REST API with real-time features with Node.js/Socket.io.',
      'Created customized D3.js graphs from scratch and integrated them with React.js.',
      'Streamlined deployment process with  Git and Jenkins.',
      'Modified existing software to improve performance by adding a "Salting" feature within Apache Hbase.',
    ],
    tech_used: [
      {
        icon: <i className="devicon-react-original" />,
        label: 'React',
      },
      {
        icon: ReduxIcon,
        label: 'Redux',
      },
      {
        icon: D3jsIcon,
        label: 'D3js',
      },
      {
        icon: <i className="devicon-nodejs-plain" />,
        label: 'Nodejs',
      },
      {
        icon: <i className="devicon-git-plain" />,
        label: 'Git',
      },
      {
        icon: HbaseIcon,
        label: 'Hbase',
      },
    ],
  },
  {
    title: 'Full-Stack Developer',
    company: 'ANSI',
    company_url: 'https://www.ansi.tn/',
    date_from: 'FEBRUARY 2016',
    date_to: 'JUIN 2016',
    tasks: [
      'Recommended architectural improvements and design solutions.',
      'Analysed and rebuilt web application from the ground up.',
      'Designed React.js application components.',
      'Met with manager on a weekly basis to provide a detailed project report and milestone updates.',
    ],
    tech_used: [
      {
        icon: <i className="devicon-react-original" />,
        label: 'React',
      },
      {
        icon: ReduxIcon,
        label: 'Redux',
      },
      {
        icon: <i className="devicon-nodejs-plain" />,
        label: 'Nodejs',
      },
      {
        icon: <i className="devicon-mongodb-plain" />,
        label: 'MongoDB',
      },
    ],
  },
];

export default class RightSection extends Component {
  render () {
    return (
      <div className="right-section">
        <div className="head">
          <div className="icon">
            <TiBeaker />
          </div>
          <div className="label">Experience</div>
        </div>
        <div className="rs-body">
          {experience.map ((itm, idx) => <Job job={itm} key={`job-${idx}`} />)}
        </div>
      </div>
    );
  }
}

const Job = ({job}) => (
  <div className="job">
    <div className="title">{job.title}</div>
    <div className="subtitle">
      <a href={job.company_url} className="company">{job.company}</a>
      <div className="date">
        {`${job.date_from} - ${job.date_to}`}
      </div>
    </div>
    <div className="description">
      <ul>
        {job.tasks.map ((itm, idx) => (
          <li key={`task-${idx}`}>
            {itm}
          </li>
        ))}
      </ul>
    </div>
    <div className="tech-used">
      {job.tech_used.map ((itm, idx) => (
        <div className="tech" key={`tech-${idx}`}>
          <div
            className={cs ({
              icon: true,
              hbase: itm.label === 'Hbase',
            })}
          >
            {itm.icon}
          </div>
          <div className="label">{itm.label}</div>
        </div>
      ))}
    </div>
  </div>
);



